import React from 'react';


function Card(props) {
    //  const res =  fetch('https://api.covid19india.org/data.json');
    //  const actualData =  res.json();
    //  console.log(actualData.statewise[0])

    React.useEffect(() => {
        fetch("https://api.covid19india.org/data.json")
            .then(res => res.json())
            .then(
                (result) => {
                    console.log('result', result.statewise)
                })
    }, [])


    return (
        <>
            <div className='Card'>

                <div className='Card2'>

                    <img src={props.imgsrc} className='IMG'></img>
                    <div class="container">
                        <h3>{props.SeriesName}</h3>

                        <a href={props.Link}><br></br>
                            <button>Click to Watch</button>
                        </a>
                    </div>
                </div>

            </div>
        </>
    )
}

